/* 🌟 Container */
.container {
  max-width: 1000px;
  margin: auto;
  animation: fadeIn 0.6s ease-in-out;
}

/* 🌟 Card wrapper */
.card {
  border-radius: 16px;
  border: 1px solid #e5e7eb;
  background: #fff;
  box-shadow: 0 6px 20px rgba(0,0,0,0.08);
  animation: slideUp 0.5s ease;
}

/* Empty cart */
.card h4 {
  font-size: 18px;
  color: #9ca3af;
  font-weight: 500;
  animation: fadeIn 1s ease;
}

/* 🌟 Cart item row */
.cart-item {
  transition: transform 0.25s ease, background 0.25s ease;
}
.cart-item:hover {
  background: #f9fafb;
  border-radius: 12px;
  transform: scale(1.01);
}

/* Product image */
.product-img {
  object-fit: cover;
  border: 1px solid #f3f4f6;
  border-radius: 12px;
  transition: transform 0.3s ease;
}
.product-img:hover {
  transform: scale(1.08) rotate(1deg);
}

/* Title */
.cart-item h6 {
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 4px;
  transition: color 0.2s ease;
}
.cart-item h6:hover {
  color: #2563eb;
}

/* Category text */
.cart-item small {
  font-size: 14px;
  color: #6b7280;
}

/* Price */
.cart-item .fw-semibold {
  font-size: 16px;
  color: #111827;
  transition: color 0.3s ease;
}
.cart-item .fw-semibold:hover {
  color: #10b981;
}

/* Quantity controls */
.cart-item input[type="button"] {
  min-width: 36px;
  height: 36px;
  padding: 0;
  font-size: 18px;
  font-weight: bold;
  border-radius: 8px;
  border: 1px solid #d1d5db;
  background: #f3f4f6;
  transition: all 0.25s ease;
  cursor: pointer;
}
.cart-item input[type="button"]:hover {
  background: #e5e7eb;
  transform: scale(1.1);
}

/* Quantity value */
.cart-item strong {
  margin: 0 10px;
  font-size: 18px;
  color: #374151;
  display: inline-block;
  animation: pulse 1.5s infinite;
}

/* Total price */
.cart-item .fw-bold {
  font-size: 17px;
  color: #10b981;
  animation: fadeIn 0.5s ease;
}

/* Remove button */
.cart-item .btn-link {
  font-size: 14px;
  font-weight: 600;
  color: #ef4444;
  transition: color 0.2s ease;
}
.cart-item .btn-link:hover {
  text-decoration: underline;
  color: #dc2626;
  transform: scale(1.05);
}

/* 🌟 Summary box */
.cart-summary {
  background: #f9fafb;
  border-radius: 12px;
  animation: slideUp 0.7s ease;
}
.cart-summary h5 {
  font-size: 20px;
  font-weight: 700;
  margin-bottom: 12px;
  animation: fadeIn 0.6s ease-in;
}
.cart-summary span {
  font-size: 16px;
}

/* Checkout button */
.cart-summary .btn-success {
  background: #10b981 !important;
  border: none;
  border-radius: 10px;
  font-weight: 700;
  transition: transform 0.3s ease, background 0.3s ease;
}
.cart-summary .btn-success:hover {
  background: #059669 !important;
  transform: translateY(-2px) scale(1.03);
  box-shadow: 0 6px 16px rgba(16,185,129,0.3);
}

/* 🌟 Animations */
@keyframes fadeIn {
  from { opacity: 0; transform: translateY(10px); }
  to   { opacity: 1; transform: translateY(0); }
}

@keyframes slideUp {
  from { opacity: 0; transform: translateY(30px); }
  to   { opacity: 1; transform: translateY(0); }
}

@keyframes pulse {
  0%   { transform: scale(1); }
  50%  { transform: scale(1.05); color: #111827; }
  100% { transform: scale(1); }
}
